.container {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
  
.menu {
    padding: 0;
    list-style-type: none;
    margin-top: 0;
    margin-bottom: 0;
}

.menuItem {
    width: 320px;
    height: 64px;
    display: flex;
    align-items: center;
}

.cont {
    display: flex;
    flex-direction: column;
}

.menuSubtitle {
    max-width: 320px;
    opacity: .4;
}

.nav__link {
    color: #8585AD;
}
    
.nav__linkActive {
    color: #F2F2F3;
}